*{
    box-sizing: border-box;
    padding: 0;
    margin: 0;
}
.title{
    font-size: 7vw;
    text-transform: uppercase;
    font-family: "Montserrat", sans-serif;
    font-weight: 100;
}
.header{
    display: flex;
    align-items: center;
    justify-content: space-evenly;
    flex-direction: column;
    height: 35vh;
    width: 100%;
}

@media (max-width: 900px) {
    .header {
      justify-content: space-evenly;
      height: 35vh;
    }
    .title {
      font-size: 11vw;
    }
  }
.inputs {
    width: 100%;
    display: flex;
    justify-content: space-around;
  }
  
  .search {
    width: 43%;
  }
  
  .select {
    width: 43%;
  }